.news b,
.news strong {
  font-weight: bolder; }

.news img {
  border-style: none; }

.news button {
  font-size: 100%;
  overflow: visible;
  text-transform: none; }

.news button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

.news button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

.news [hidden] {
  display: none; }

.playlist {
  width: 1440px;
  height: 100px;
  margin: 0 auto;
  outline: 4px solid green;
  padding-left: 20px;
  padding-right: 20px; }

.playlist__songs {
  display: block;
  width: 675px;
  height: 100px;
  outline: 4px solid tomato; }

.playlist__mood {
  padding-left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none; }

.playlist__tone {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1; }

.badge {
  display: block; }

.badge--c {
  background-color: #2e97b8; }

.badge--cs {
  background-color: #2e55b8; }

.badge--d {
  background-color: #f9e324; }

.badge--ds {
  background-color: #f69f1c; }

.badge--e {
  background-color: #089a5d; }

.badge--f {
  background-color: #d23e0f; }

.badge--fs {
  background-color: #a6523b; }

.badge--g {
  background-color: #940fd2; }

.badge--gs {
  background-color: #660096; }

.badge--a {
  background-color: #f25959; }

.badge--as {
  background-color: #2e97b8; }

.badge--b {
  background-color: #55d5fe; }
